<template>
  <div>
    <search-box :searchData="searchData" :table="table"></search-box>

    <resize-table>
    <table class="table">
      <thead>
      <tr>
        <th width="35%">标题</th>
        <th width="15%">发布时间</th>
        <th width="20%">范围</th>
        <th width="10%">阅读人数</th>
        <th width="20%">操作</th>
      </tr></thead>
      <tbody>
      <tr v-for="item in table.list">
        <td>{{item.title}}</td>
        <td>{{item.createdTime | substrTime }}</td>
        <td><span v-for="scopeItem in item.scopeList"> {{scopeItem.scopeName}}</span></td>
        <td style="position: relative">
          <span>{{item.readNum}}</span>
          <i class="el-icon-view oper view" @click="onDialogClick(item.title,item.id)"></i>
        </td>
        <td>
          <span @click="$router.push(`/research-orsay/research/publicity?id=`+item.id+``)" class="oper">编辑</span>
          <span @click="remove(item)" class="oper">删除</span>
        </td>
      </tr></tbody>
    </table>
    </resize-table>
    <no-data v-show="!table.noData"></no-data>
    <paging :data.sync="table"></paging>

    <!--阅读详情-->
    <el-dialog :title="title"
               :visible.sync="dialogVisible"
               width="30%">
      <el-tabs @tab-click="onSwitchClick" class="dialog_table">
        <el-tab-pane :label="dialogTable.total+'人已读'"></el-tab-pane>
        <el-tab-pane :label="noDialogTable.total+'人未读'"></el-tab-pane>
        <!--已读-->

        <resize-table>
        <table class="table" v-show="!dialogSwitch">
          <thead>
          <tr>
            <th width="33%">姓名</th>
            <th width="33%">职务</th>
            <th width="33%">阅读时间</th>
          </tr></thead>
          <tbody>
          <tr v-for="item in dialogTable.list">
            <td>{{item.userName}}</td>
            <td>{{item.position}}</td>
            <td>{{item.createdTime | substrTime}}</td>
          </tr></tbody>
        </table>
        </resize-table>
        <paging :data.sync="dialogTable" v-show="!dialogSwitch"></paging>
        <!--未读-->

        <resize-table>

        <table class="table" v-show="dialogSwitch">
          <thead>
          <tr>
            <th width="50%">姓名</th>
            <th width="50%">职务</th>
          </tr></thead>
          <tbody>
          <tr v-for="item in noDialogTable.list">
            <td>{{item.userName}}</td>
            <td>{{item.position}}</td>
          </tr></tbody>
        </table>
        </resize-table>
        <paging :data.sync="noDialogTable" v-show="dialogSwitch"></paging>
      </el-tabs>
    </el-dialog>
  </div>

</template>
<script>
  export default {
    data () {
      return {
        searchData: [{type: 'input', placeholder: '标题'}],
        table: {
          url: '/notice/queryNoticePage.do',
          el: '.table',
          total: 0,
          noData: true,
          list: [],
          query: {
            noticeType: 'SUBJECT_STUDY',
            keywords: null
          }
        },
        // 已读
        dialogTable: {
          url: '/notice/queryNoticeReadRecordPage.do',
          el: '.dialog_table',
          total: 0,
          noData: true,
          list: [],
          query: {}
        },
        // 未读
        noDialogTable: {
          url: '/notice/queryNoticeNoReadRecordPage.do',
          el: '.dialog_table',
          total: 0,
          noData: true,
          list: [],
          query: {}
        },
        dialogVisible: false,
        title: null,
        dialogSwitch: false
      }
    },
    methods: {
      // 切换未读已读
      onSwitchClick () {
        this.dialogSwitch = !this.dialogSwitch
      },
      // 点击出现阅读
      onDialogClick (title, id) {
        this.title = title
        this.dialogTable.query.id = id
        this.noDialogTable.query.id = id
        this.$search(this.dialogTable)
        this.$search(this.noDialogTable)
        this.dialogVisible = true
      },
      // 删除
      async remove (item) {
        await this.$confirm(`您确认删除【${item.title}】吗?`, '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        })
        await this.$ajax({
          url: '/notice/deleteNotice.do',
          query: {
            id: item.id,
            isDelete: 'YES'
          }
        })
        this.$remind('删除成功')
        this.$search(this.table)
      }
    },
    mounted () {
      this.$search(this.table)
    }
  }
</script>
<style lang="sass" scoped>
    .view
      position: absolute
      right: 8px
      font-size: $font-large
      cursor: pointer
      color: $fc-def !important
      padding: 0 !important

    /deep/ .el-dialog
      width: 50% !important
      .el-dialog__title
        padding: 10px 0
        font-size: $font-large+4
</style>
